import { NextResponse } from 'next/server';
import { supabase } from '../../../utils/supabase';
import { transformDbRecipesToFrontend, DbRecipe } from '../../../utils/dataMapper';
import { logError } from '../../../utils/errorLogger';

// 强制将路由标记为动态，避免在构建时执行
export const dynamic = 'force-dynamic';
export const runtime = 'edge';

// 定义API响应类型
interface ApiErrorResponse {
  error: string;
  details?: string;
}

/**
 * 处理GET请求，返回菜谱数据
 */
export async function GET() {
  // 检测是否在构建过程中
  const isSSG = process.env.NODE_ENV === 'production' && typeof window === 'undefined';
  
  // 在构建过程中返回空数组，避免触发数据库查询
  if (isSSG) {
    console.log('API路由: 静态构建环境，跳过Supabase查询');
    return new NextResponse(JSON.stringify([]), {
      status: 200,
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type'
      }
    });
  }
  
  try {
    console.log('API路由: 开始从Supabase加载菜谱数据...');
    console.log('Supabase URL:', process.env.NEXT_PUBLIC_SUPABASE_URL || '未设置');
    console.log('Supabase Key是否配置:', process.env.NEXT_PUBLIC_SUPABASE_ANON_KEY ? '已配置' : '未配置');
    
    // 首先验证数据库连接是否正常
    try {
      console.log('验证Supabase连接...');
      const { data: testData, error: testError } = await supabase.from('CHrecipes').select('id').limit(1);
      
      if (testError) {
        console.error('Supabase连接测试失败:', testError);
        logError('API', '验证Supabase连接', testError);
        throw new Error(`Supabase连接测试失败: ${testError.message}`);
      }
      
      console.log('Supabase连接正常，继续查询所有数据...');
    } catch (connErr) {
      console.error('Supabase连接验证失败:', connErr);
      logError('API', '验证Supabase连接', connErr);
      const errorResponse: ApiErrorResponse = { error: '数据库连接失败，请稍后再试' };
      return new NextResponse(JSON.stringify(errorResponse), {
        status: 503,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        }
      });
    }
    
    const { data, error } = await supabase.from('CHrecipes').select('*');
    
    if (error) {
      console.error('API路由: Supabase查询错误:', error);
      logError('API', 'Supabase查询所有菜谱', error);
      const errorResponse: ApiErrorResponse = { error: `数据库查询失败: ${error.message}` };
      return new NextResponse(JSON.stringify(errorResponse), {
        status: 500,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        }
      });
    }
    
    if (!data || data.length === 0) {
      console.warn('API路由: 没有找到菜谱数据');
      return new NextResponse(JSON.stringify([]), {
        status: 200,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        }
      });
    }
    
    console.log(`API路由: 成功从Supabase加载数据，共 ${data.length} 条`);
    
    try {
      // 使用数据映射转换函数处理结果
      console.log('开始转换数据...');
      const transformedData = transformDbRecipesToFrontend(data as unknown as DbRecipe[]);
      console.log(`数据转换完成，共 ${transformedData.length} 条`);
      
      // 返回数据并添加CORS响应头
      return new NextResponse(JSON.stringify(transformedData), {
        status: 200,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        }
      });
    } catch (transformError) {
      console.error('API路由: 数据转换失败:', transformError);
      logError('API', '转换菜谱数据', transformError);
      const errorResponse: ApiErrorResponse = { error: '数据转换失败' };
      return new NextResponse(JSON.stringify(errorResponse), {
        status: 500,
        headers: {
          'Content-Type': 'application/json',
          'Access-Control-Allow-Origin': '*',
          'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
          'Access-Control-Allow-Headers': 'Content-Type'
        }
      });
    }
  } catch (error) {
    console.error('API路由: 加载菜谱数据失败:', error);
    logError('API', '加载菜谱数据', error);
    const errorResponse: ApiErrorResponse = { 
      error: '无法加载菜谱数据', 
      details: error instanceof Error ? error.message : String(error) 
    };
    return new NextResponse(JSON.stringify(errorResponse), {
      status: 500,
      headers: {
        'Content-Type': 'application/json',
        'Access-Control-Allow-Origin': '*',
        'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
        'Access-Control-Allow-Headers': 'Content-Type'
      }
    });
  }
}

/**
 * 处理OPTIONS请求，用于CORS预检
 */
export async function OPTIONS() {
  return new NextResponse(null, {
    status: 204,
    headers: {
      'Access-Control-Allow-Origin': '*',
      'Access-Control-Allow-Methods': 'GET, POST, OPTIONS',
      'Access-Control-Allow-Headers': 'Content-Type'
    }
  });
} 