/**
 * 数据映射转换模块
 * 负责在数据库记录和前端模型之间进行转换
 */
import { Recipe } from '../data/recipes';
import { Flavor, Difficulty, Cuisine, CookingMethod, DietaryRestriction } from '../types/recipe';
import { logError, logWarning } from './errorLogger';

/**
 * 定义数据库记录类型
 */
export interface DbRecipe {
  id: string;
  菜名: string;
  菜系: string;
  口味特点: string | any[];
  烹饪技法: string | any[];
  食材: string | any[];
  调料: string | any[];
  步骤: string | any[];
  注意事项: string | any[];
  烹饪时间: number | string | any;
  烹饪难度: string | any;
  是否无麸质: boolean;
  是否清真: boolean;
  是否纯素: boolean;
  调料分类: string | any[];
  a食材分类: string | any[];
  created_at?: string;
  updated_at?: string;
  user_id?: string;
}

/**
 * 安全解析JSONB字段
 */
function safeParseJsonb<T>(value: any, defaultValue: T): T {
  if (value === null || value === undefined) return defaultValue;
  
  if (Array.isArray(value)) return value as T;
  
  if (typeof value === 'string') {
    if (value.trim() === '') return defaultValue;
    
    try {
      // 尝试解析为JSON对象
      const parsed = JSON.parse(value);
      return parsed as T;
    } catch (e) {
      // 如果不是有效的JSON，则返回字符串数组
      logWarning('DataMapper', 'safeParseJsonb', `非JSON字符串值: ${value}`);
      return [value] as unknown as T;
    }
  }
  
  // 处理其他类型的值
  if (typeof value === 'object') {
    try {
      return value as T;
    } catch (e) {
      logWarning('DataMapper', 'safeParseJsonb', `无法处理的对象类型: ${JSON.stringify(value)}`);
      return defaultValue;
    }
  }
  
  // 处理基本类型
  logWarning('DataMapper', 'safeParseJsonb', `无法处理的值类型: ${typeof value}, 值: ${String(value)}`);
  return defaultValue;
}

/**
 * 将数据库记录转换为前端模型
 */
export function transformDbToFrontend(dbRecipe: DbRecipe): Recipe {
  try {
    // 处理饮食标签
    const dietaryRestrictions: DietaryRestriction[] = [];
    if (dbRecipe.是否纯素) dietaryRestrictions.push('纯素' as DietaryRestriction);
    if (dbRecipe.是否清真) dietaryRestrictions.push('清真' as DietaryRestriction);
    if (dbRecipe.是否无麸质) dietaryRestrictions.push('无麸质' as DietaryRestriction);
    
    // 安全解析JSONB字段
    const flavors = safeParseJsonb<Flavor[]>(dbRecipe.口味特点, []);
    const cookingMethod = safeParseJsonb<CookingMethod[]>(dbRecipe.烹饪技法, []);
    const ingredients = safeParseJsonb<string[]>(dbRecipe.食材, []);
    const seasonings = safeParseJsonb<string[]>(dbRecipe.调料, []);
    
    // 解析步骤内容，提取准备步骤和烹饪步骤
    let preparationSteps: string[] = [];
    let cookingSteps: string[] = [];
    let steps: string[] = [];
    
    if (typeof dbRecipe.步骤 === 'object' && dbRecipe.步骤 !== null) {
      // 检查是否符合{准备步骤: [...], 烹饪步骤: [...]}格式
      const stepsObj = dbRecipe.步骤 as unknown as Record<string, any>;
      if (stepsObj.hasOwnProperty('准备步骤') && stepsObj.hasOwnProperty('烹饪步骤')) {
        preparationSteps = Array.isArray(stepsObj['准备步骤']) ? stepsObj['准备步骤'] : [];
        cookingSteps = Array.isArray(stepsObj['烹饪步骤']) ? stepsObj['烹饪步骤'] : [];
        // 为向后兼容，合并所有步骤到steps字段
        steps = [...preparationSteps, ...cookingSteps];
      } else {
        // 如果不是新格式，则按原样处理
        steps = safeParseJsonb<string[]>(dbRecipe.步骤, []);
        cookingSteps = steps; // 向后兼容
      }
    } else {
      steps = safeParseJsonb<string[]>(dbRecipe.步骤, []);
      cookingSteps = steps; // 向后兼容
    }
    
    const tips = safeParseJsonb<string[]>(dbRecipe.注意事项, []);
    
    // 处理烹饪时间
    let cookingTime = 30; // 默认值
    if (typeof dbRecipe.烹饪时间 === 'number') {
      cookingTime = dbRecipe.烹饪时间;
    } else if (typeof dbRecipe.烹饪时间 === 'string') {
      const parsed = parseInt(dbRecipe.烹饪时间, 10);
      if (!isNaN(parsed)) cookingTime = parsed;
    } else if (dbRecipe.烹饪时间 && typeof dbRecipe.烹饪时间 === 'object') {
      // 尝试从对象中解析时间
      try {
        const timeStr = JSON.stringify(dbRecipe.烹饪时间);
        const match = timeStr.match(/\d+/);
        if (match) cookingTime = parseInt(match[0], 10);
      } catch (e) {
        logWarning('DataMapper', 'transformDbToFrontend', `无法解析烹饪时间: ${e instanceof Error ? e.message : String(e)}`);
      }
    }
    
    // 转换为Recipe对象
    return {
      id: dbRecipe.id || generateTempId(),
      name: dbRecipe.菜名 || '未命名菜谱',
      cuisine: dbRecipe.菜系 as Cuisine || '其他',
      description: '', // 数据库中没有直接对应的字段
      ingredients: ingredients,
      seasonings: seasonings,
      flavors: flavors,
      difficulty: dbRecipe.烹饪难度 as Difficulty || '中等',
      cookingTime: cookingTime,
      steps: steps,
      preparationSteps: preparationSteps,
      cookingSteps: cookingSteps,
      tips: tips,
      imageUrl: '/images/placeholder-food.jpg', // 默认图片
      cookingMethod: cookingMethod,
      dietaryRestrictions: dietaryRestrictions,
      tags: [] // 数据库中没有直接对应的字段
    };
  } catch (error) {
    logError('DataMapper', 'transformDbToFrontend', error, { recipeId: dbRecipe?.id });
    
    // 返回一个安全的默认菜谱对象
    return {
      id: dbRecipe?.id || generateTempId(),
      name: dbRecipe?.菜名 || '转换错误的菜谱',
      cuisine: '其他' as Cuisine,
      description: '数据转换过程中发生错误',
      ingredients: [],
      seasonings: [],
      flavors: [],
      difficulty: '简单' as Difficulty,
      cookingTime: 30,
      steps: [],
      preparationSteps: [],
      cookingSteps: [],
      tips: [],
      imageUrl: '/images/placeholder-food.jpg',
      cookingMethod: [],
      dietaryRestrictions: [],
      tags: []
    };
  }
}

/**
 * 生成临时ID
 */
function generateTempId(): string {
  return 'temp-' + Date.now().toString(36) + Math.random().toString(36).substring(2, 7);
}

/**
 * 将前端模型转换为数据库记录
 */
export function transformFrontendToDb(recipe: Recipe): DbRecipe {
  // 处理饮食标签
  const 是否纯素 = Array.isArray(recipe.dietaryRestrictions) && recipe.dietaryRestrictions.includes('纯素' as DietaryRestriction) || false;
  const 是否清真 = Array.isArray(recipe.dietaryRestrictions) && recipe.dietaryRestrictions.includes('清真' as DietaryRestriction) || false;
  const 是否无麸质 = Array.isArray(recipe.dietaryRestrictions) && recipe.dietaryRestrictions.includes('无麸质' as DietaryRestriction) || false;
  
  return {
    id: recipe.id,
    菜名: recipe.name,
    菜系: recipe.cuisine || '',
    口味特点: Array.isArray(recipe.flavors) ? recipe.flavors : [],
    烹饪技法: Array.isArray(recipe.cookingMethod) ? recipe.cookingMethod : [],
    食材: Array.isArray(recipe.ingredients) ? recipe.ingredients : [],
    调料: Array.isArray(recipe.seasonings) ? recipe.seasonings : [],
    步骤: Array.isArray(recipe.steps) ? recipe.steps : (Array.isArray(recipe.cookingSteps) ? recipe.cookingSteps : []),
    注意事项: Array.isArray(recipe.tips) ? recipe.tips : [],
    烹饪时间: recipe.cookingTime || 30,
    烹饪难度: recipe.difficulty || '中等',
    是否无麸质: 是否无麸质,
    是否清真: 是否清真,
    是否纯素: 是否纯素,
    调料分类: [],
    a食材分类: []
  };
}

/**
 * 批量转换数据库记录到前端模型
 */
export function transformDbRecipesToFrontend(dbRecipes: DbRecipe[]): Recipe[] {
  if (!Array.isArray(dbRecipes)) {
    logError('DataMapper', 'transformDbRecipesToFrontend', `接收到非数组输入: ${typeof dbRecipes}`, { value: dbRecipes });
    return [];
  }
  
  console.log(`开始批量转换 ${dbRecipes.length} 个菜谱数据`);
  const results: Recipe[] = [];
  
  for (let i = 0; i < dbRecipes.length; i++) {
    try {
      if (!dbRecipes[i]) {
        logWarning('DataMapper', 'transformDbRecipesToFrontend', `索引 ${i} 处的数据为空`);
        continue;
      }
      
      const recipe = transformDbToFrontend(dbRecipes[i]);
      results.push(recipe);
    } catch (error) {
      logError('DataMapper', 'transformDbRecipesToFrontend', error, { 
        index: i,
        data: dbRecipes[i] ? { id: dbRecipes[i].id, name: dbRecipes[i].菜名 } : null 
      });
      // 继续处理下一条，不中断整个批处理
    }
  }
  
  console.log(`批量转换完成，成功转换 ${results.length} / ${dbRecipes.length} 个菜谱`);
  return results;
} 