/**
 * 加载所有菜谱数据
 */
export async function loadAllRecipes(): Promise<Recipe[]> {
  try {
    console.log('服务器端直接加载菜谱数据...');
    
    // 首先快速加载第一批数据以保证页面响应速度
    let allRecipes: any[] = [];
    const { data: firstBatch, error: firstError } = await supabase
      .from('CHrecipes')
      .select('*')
      .range(0, 999);
      
    if (firstError) {
      console.error('首批数据加载失败:', firstError);
      throw firstError;
    }
    
    if (firstBatch && firstBatch.length > 0) {
      console.log(`批次 1 加载了 ${firstBatch.length} 条菜谱`);
      allRecipes = [...firstBatch];
      
      // 如果是客户端环境，异步加载剩余数据
      if (typeof window !== 'undefined') {
        console.log('客户端环境，准备在后台加载剩余数据...');
        // 使用setTimeout确保不阻塞UI
        setTimeout(() => {
          loadRemainingRecipes(1000)
            .then(remainingData => {
              console.log(`后台加载了额外的 ${remainingData.length} 条菜谱数据`);
              // 尝试更新全局状态或存储到IndexedDB
              if (typeof window.updateRecipesCache === 'function') {
                window.updateRecipesCache([...allRecipes, ...remainingData]);
              } else {
                // 存储到sessionStorage作为备选方案
                try {
                  sessionStorage.setItem('additionalRecipes', JSON.stringify(remainingData));
                  console.log('已将额外数据存储到sessionStorage');
                } catch (e) {
                  console.warn('无法存储额外数据到sessionStorage:', e);
                }
              }
            })
            .catch(err => {
              console.warn('后台加载额外数据失败:', err);
            });
        }, 3000); // 延迟3秒后开始加载，让主要UI先渲染完成
      }
    }
    
    console.log(`服务器端成功加载 ${allRecipes.length} 个菜谱`);
    
    // 启动数据预热
    // 在首页预加载热门菜系的数据
    if (typeof window !== 'undefined') {
      setTimeout(() => {
        console.log('预加载热门菜系数据...');
        ['中餐-家常菜', '中餐-小吃'].forEach(cuisine => {
          loadFilteredRecipes({ cuisine }).catch(e => 
            console.log(`预加载${cuisine}数据失败:`, e)
          );
        });
      }, 3000);
    }
    
    // 使用数据映射转换函数处理结果
    return transformDbRecipesToFrontend(allRecipes as unknown as DbRecipe[]);
  } catch (error) {
    console.error('加载菜谱数据时出错:', error);
    console.log('⚠️ 使用示例数据模式');
    const sampleData = useSampleData();
    return sampleData;
  }
}

/**
 * 后台加载剩余菜谱数据
 * @param startIndex 起始索引
 */
async function loadRemainingRecipes(startIndex: number): Promise<any[]> {
  let allRemainingRecipes: any[] = [];
  let page = Math.floor(startIndex / 1000);
  const pageSize = 1000;
  let hasMore = true;

  try {
    // 继续加载剩余批次
    while (hasMore) {
      const start = startIndex + (page - Math.floor(startIndex / 1000)) * pageSize;
      const end = start + pageSize - 1;
      
      console.log(`后台加载菜谱批次 ${page+1}，范围 ${start}-${end}`);
      const { data, error } = await supabase
        .from('CHrecipes')
        .select('*')
        .range(start, end);
      
      if (error) {
        console.error(`后台加载菜谱批次 ${page+1} 出错:`, error);
        break;
      }
      
      if (!data || data.length === 0) {
        console.log(`后台批次 ${page+1} 未返回数据，加载完成`);
        hasMore = false;
        break;
      }
      
      console.log(`后台批次 ${page+1} 加载了 ${data.length} 条菜谱`);
      allRemainingRecipes = [...allRemainingRecipes, ...data];
      
      if (data.length < pageSize) {
        console.log(`后台批次 ${page+1} 数据量少于每页数量，加载完成`);
        hasMore = false;
      } else {
        page++;
      }
      
      // 适当延迟，避免一次性发送太多请求
      await new Promise(resolve => setTimeout(resolve, 1000));
    }
    
    return allRemainingRecipes;
  } catch (error) {
    console.error('后台加载剩余数据出错:', error);
    return [];
  }
}

